<script>
	import { reveal } from 'svelte-reveal';

	let count = 0;

	function handleClick() {
		count += 1;
		if (count >= 1000) {
			window.location.href = "https://www.youtube.com/watch?v=dQw4w9WgXcQ";
		}
	}
</script>

<main class="flex flex-col items-center">
	<div class="h-screen flex flex-col">
		<div class="mt-auto animate-reveal">
			<h2 class="uppercase text-center animate-pulse">REIMNOP</h2>
		</div>
		<div class="mt-auto opacity-0 animate-reveal2">
			<p class="text-center italic text-gray-700 animate-bounce">scroll down</p>
		</div>
	</div>

	<!-- old stuff -->
	<div class="h-screen grid justify-center content-center">
		<div class="flex flex-col items-center">
			<h3 class="text-center uppercase" use:reveal>Website is under construction</h3>
			<h4 class="text-center text-gray-300" use:reveal={{ delay: 250 }}>Check again later!</h4>
			<h5 class="text-center mt-16" use:reveal={{ delay: 500 }}>While waiting for a proper website,<br>you can press this little button for fun!</h5>
			<div class="flex flex-row" use:reveal={{ delay: 750 }}>
				<div class="w-32">
					<h4 class="text-center">{count}</h4>
				</div>
				<button class="w-32 h-8 bg-gray-600 rounded shadow duration-200 hover:bg-gray-700 active:bg-gray-800 active:scale-110" on:click="{handleClick}">Click me!</button>
			</div>
			<!-- sneaky easter egg -->
			{#if count >= 900}
				<p class="text-gray-600 text-center">STOP NOW. This is your last warning.</p>
			{:else if count >= 800}
				<p class="text-gray-600 text-center">Do you think I'm joking?</p>
			{:else if count >= 700}
				<p class="text-gray-600 text-center">You need a life. Seriously.</p>
			{:else if count >= 600}
				<p class="text-gray-600 text-center">For real. Go outside. Touch some grass.</p>
			{:else if count >= 500}
				<p class="text-gray-600 text-center">Okay maybe it's time to stop.</p>
			{/if}
		</div>
	</div>
</main>